.faq-spoiler__bar {
  position: relative;
  padding-left: 50px;
  font-weight: 300;
  font-size: 20px;
  color: #444444;
  border: 1px solid #05b6df;
  border-radius: 15px;
  cursor: pointer;
  transition: all 0.3s ease; }
